import {IPasswordInput} from "./IPasswordInput";
import {useFormContext} from "react-hook-form";
import {Badge, FormControl, FormGroup, FormLabel} from "react-bootstrap";

export const PasswordInput = (props: IPasswordInput) => {
    const {register, formState: {errors}} = useFormContext()

    return <FormGroup className="mb-3">
        <FormLabel>{props.label}</FormLabel>
        <FormControl {...register(props.name, {
            required: {value: true, message: "Заполните поле."}
        })} type="password"/>
        {errors[props.name]?.message &&
            <Badge bg="warning">{errors[props.name]?.message?.toString()}</Badge>
        }
    </FormGroup>
}
