import {RouteObject} from "react-router-dom";
import {Index} from "../../../page/user/index/Index";
import {Index as SubscribeIndex} from "../../../page/user/subscribe/Index";
import {Index as HelpIndex} from "../../../page/user/help/Index";

export const userRoute : RouteObject[] = [
    {
        path: "/",
        element: <Index/>
    },
    {
        path: "/subscribe",
        element: <SubscribeIndex/>
    },
    {
        path: "/help",
        element: <HelpIndex/>
    }
]
