import {observer} from "mobx-react-lite";
import {FormProvider, useForm} from "react-hook-form";
import {PasswordInput} from "../../../../common/component/input/password/PasswordInput";
import {SetPasswordInputs} from "../../../../common/component/input/set-password/SetPasswordInputs";
import {useEffect} from "react";
import {checkUserState, setPasswordAsync, state} from "../store";
import {UserState} from "../model/UserState";
import {Alert, Button, Form, FormGroup} from "react-bootstrap";
import {TextInput} from "../../../../common/component/input/text/TextInput";

export const LinkLogin = observer((props: {telegramUserId: string, guid: string}) => {
    const hookForm = useForm({
        mode: "all"
    });

    useEffect(() => {
        checkUserState(props.telegramUserId, props.guid)
    }, [])

    return <>
        <FormProvider {...hookForm}>
            {state.loginForm?.state === UserState.NotFound &&
                <Alert variant="danger">
                    Пользователь не найден.
                </Alert>
            }
            {state.loginForm?.state === UserState.NeedSetPassword &&
                <Form onSubmit={hookForm.handleSubmit(setPasswordAsync)}>
                    <Alert>При первом входе в личный кабинет, необходимо установить логин и пароль.</Alert>
                    <TextInput
                        label="Логин:"
                        name="login"
                        isRequired={true}/>
                    <SetPasswordInputs/>
                    <Button className="mt-4" type="submit">
                        Сохранить пароль
                    </Button>
                </Form>
            }
            {state.loginForm?.state === UserState.Ready &&
                <Form onSubmit={hookForm.handleSubmit((form) => console.log(form))}>
                    <PasswordInput
                        label="Введите пароль:"
                        name="password"/>
                    <FormGroup>
                        <Button>
                            Войти
                        </Button>
                        <Button>
                            Восстановить пароль
                        </Button>
                    </FormGroup>
                </Form>
            }
        </FormProvider>
    </>
})
