import {Button, Form, Modal, ModalBody, ModalFooter, ModalHeader, ModalTitle} from "react-bootstrap";
import {FormProvider, useForm} from "react-hook-form";
import {observer} from "mobx-react-lite";
import {state, switchVisibleEditProfileModal} from "../store";

export const EditProfile = observer(() => {
    const hookForm = useForm();
    return <FormProvider {...hookForm}>
        <Form>
            <Modal show={state.isProfileEditShow}>
                <ModalHeader>
                    <ModalTitle>Редактировать профиль</ModalTitle>
                </ModalHeader>
                <ModalBody>

                </ModalBody>
                <ModalFooter>
                    <Button variant="success" type="submit">Сохранить</Button>
                    <Button variant="danger" type="button" onClick={switchVisibleEditProfileModal}>Закрыть</Button>
                </ModalFooter>
            </Modal>
        </Form>
    </FormProvider>
})
