import {Nav, Offcanvas, OffcanvasBody, OffcanvasHeader, OffcanvasTitle} from "react-bootstrap";
import {Link} from "react-router-dom";
import {CreditCard2FrontFill, HouseDoorFill, InfoCircleFill, List, PersonFill} from "react-bootstrap-icons";
import {useState} from "react";

export const LeftMenu = () => {
    const [isShow, setShow] = useState(false);

    const setHide = () => setShow(false)

    return <>
        {/*Обычное меню - Начало*/}
        <div className="nav d-lg-flex d-none flex-column justify-content-between py-2 left-menu">
            <Nav className="flex-column mx-5">
                <Link className="nav-link d-flex justify-content-center align-items-center" to="/">
                    <HouseDoorFill className="me-3"/>
                    <span>Главная</span>
                </Link>
                <Link className="nav-link d-flex justify-content-center align-items-center" to="/subscribe">
                    <CreditCard2FrontFill className="me-3"/>
                    <span>Подписка</span>
                </Link>
                <Link className="nav-link d-flex justify-content-center align-items-center" to="/help">
                    <InfoCircleFill className="me-3"/>
                    <span>Помощь</span>
                </Link>
            </Nav>
            <div className="text-center">
                <span>LockVpn 2023</span>
            </div>
        </div>
        {/*Обычное меню - Конец*/}

        {/*Мобильное меню - Начало*/}
        <div className="d-block d-lg-none menu-button" onClick={() => setShow(true)}>
            <List width={35} height={35}/>
        </div>

        <Offcanvas show={isShow} onHide={setHide}>
            <OffcanvasHeader closeButton>
                <OffcanvasTitle>Меню</OffcanvasTitle>
            </OffcanvasHeader>
            <OffcanvasBody>
                    <Nav className="flex-column mx-5">
                        <Link className="nav-link d-flex justify-content-center align-items-center" to="/" onClick={setHide}>
                            <HouseDoorFill className="me-3"/>
                            <span>Главная</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-center align-items-center" to="/subscribe" onClick={setHide}>
                            <CreditCard2FrontFill className="me-3"/>
                            <span>Подписка</span>
                        </Link>
                        <Link className="nav-link d-flex justify-content-center align-items-center" to="/help" onClick={setHide}>
                            <InfoCircleFill className="me-3"/>
                            <span>Помощь</span>
                        </Link>
                    </Nav>
            </OffcanvasBody>
        </Offcanvas>
        {/*Мобильное меню - Конец*/}
    </>
}
