import {Badge, Button} from "react-bootstrap";
import {switchVisibleChangePasswordModal, switchVisibleEditProfileModal} from "../store";

export const Profile = () => {
    return <>
        <div>
            <h6>Профиль:</h6>
        </div>
        <div className="d-block d-lg-flex justify-content-around">
            <div className="flex-fill">
                <div>
                    <span>Логин:</span> Иван
                </div>

                <div>
                    <span>Почта:</span> test@gmail.com
                </div>
            </div>
            <div className="flex-fill">
                <div>
                    <span>Дата регистрации: </span>{new Date().toLocaleDateString()}
                </div>

                <div>
                    <span>Доступ: </span>
                    <Badge bg="success">Активен (до {new Date().toLocaleDateString()})</Badge>
                </div>
            </div>
        </div>
        <div className="d-flex mt-2">
            <Button className="me-2" variant="primary" size="sm" onClick={switchVisibleEditProfileModal}>Редактировать</Button>
            <Button variant="primary" size="sm" onClick={switchVisibleChangePasswordModal}>Сменить пароль</Button>
        </div>
        <hr/>
    </>

}
