import {IState} from "./modal/IState";
import {action, observable} from "mobx";


const state : IState = observable({
    profile: undefined,
    isProfileEditShow: false,
    isChangePasswordShow: false
});

const getIndexAsync = action(async() => {

})

const switchVisibleChangePasswordModal = action(() => {
    if(state.isChangePasswordShow)
    {
        state.isChangePasswordShow = false;
    }
    else
    {
        state.isChangePasswordShow = true;
    }
})

const switchVisibleEditProfileModal = action(() => {
    if(state.isProfileEditShow)
    {
        state.isProfileEditShow = false;
    }
    else
    {
        state.isProfileEditShow = true;
    }
})

export {state, getIndexAsync, switchVisibleChangePasswordModal, switchVisibleEditProfileModal}
