import React from "react";
import "./style.css";
import {useSearchParams} from "react-router-dom";
import {LinkLogin} from "./component/LinkLogin";
import {Alert, Badge, Image} from "react-bootstrap";

export const Index = () => {
    const [searchParams] = useSearchParams();
    const telegramUserId = searchParams.get("TelegramUserId")!;
    const guid = searchParams.get("Guid")!;

    return <div>
        <div className="d-flex flex-column justify-content-center align-items-center vh-100">
            <div className="d-flex justify-content-center mb-4">
                <img className="login-logo" src="/img/logo.png"/>
            </div>

            <div className="d-flex flex-column align-items-center login-form">
                {(telegramUserId && guid) &&
                    <LinkLogin telegramUserId={telegramUserId} guid={guid}/>
                }
                {(!telegramUserId || !guid) &&
                    <Alert variant="danger">
                        Для входа в личный кабинет, перейдите по ссылке из <a href="https://t.me/lock_vpn_bot">телеграм бота</a>.
                    </Alert>
                }
            </div>
        </div>
    </div>
}
